import { client } from '@/app/sanity/client'

export const revalidate = 60 // optional ISR

// Make the component async to fetch data on the server
export default async function TemplatesPage() {
  // Fetch categories + templates
  const categories = await client.fetch(`
    *[_type == "templateCategory"]{
      _id,
      title,
      slug,
      description,
      "templates": *[_type == "emailTemplate" && category._ref == ^._id]{
        _id,
        title,
        subject,
        body,
        design,
        tags
      }
    }
  `)

  return (
    <div className="p-8">
      <h1 className="text-3xl font-bold mb-6">Email Templates</h1>
      {categories.map((cat) => (
        <div key={cat._id} className="mb-8 border p-4 rounded">
          <h2 className="text-2xl font-semibold">{cat.title}</h2>
          <p className="text-gray-600 mb-4">{cat.description}</p>
          <ul className="space-y-2">
            {cat.templates.map((temp) => (
              <li key={temp._id} className="border p-2 rounded flex justify-between items-center">
                <div>
                  <strong>{temp.title}</strong> - {temp.subject}
                </div>
                <button
                  className="bg-blue-500 text-white px-3 py-1 rounded"
                  onClick={() => {
                    // Navigate to your send-email page with template
                    // Example: router.push(`/crm/send/${temp._id}`)
                  }}
                >
                  Use Template
                </button>
              </li>
            ))}
          </ul>
        </div>
      ))}
    </div>
  )
}
